<template>
    <div class="resultboard">
        <img src="@/assets/images/result-title.png" class="resultboard-title"/>
        <div class="resultboard-result">
            <div :class="{
                'resultboard-ranking-first': rank == 0,
                'resultboard-ranking-second': rank == 1,
                'resultboard-ranking-third': rank == 2,
                }" class="resultboard-ranking"
            v-for="(entity, rank) in ranking" :key="rank">
                <img v-if="rank < 3"
                :src="require(`@/assets/images/rank_${ rank + 1 }.png`)" class="resultboard-ranking-ico"/>
                <div v-else></div>
                <div class="resultboard-ranking-name">{{ entity }}</div>
            </div>
        </div>
        <button class="resultboard-gomain" @click="goMain">메인 화면으로</button>
    </div>
</template>

<script setup>
import { useRouter } from 'vue-router';

const router = useRouter();
const ranking = history.state.ranking;

function goMain(){
    router.replace({name: "home"});
}
</script>

<style scoped>
@import url("@/assets/css/resultboard.css");
</style>