<template>
    <div class="singleplay">
        <ScoreBoard 
        :entityList="entityList" 
        :remainTurns="remainTurns"
        :currentTurn="currentTurn"
        :diceList="diceList"
        @update-top-score="updateScore" @update-bottom-score="updateScore"></ScoreBoard>
        <div>
            <DiceSlot :diceList="diceList" @release-dice="releaseDice"></DiceSlot>
            <DiceTray :diceList="diceList" :remainRerolls="remainRerolls" @fix-dice="fixDice"></DiceTray>
        </div>
        <div style="display: flex; align-items: center; flex-direction: column;">
            <LiveBoard :remainRerolls="remainRerolls"></LiveBoard>
            <RerollBoard :remainRerolls="remainRerolls" @roll-dice="rollDice"></RerollBoard>
        </div>
    </div>
</template>

<script setup>
import { onBeforeUnmount, onMounted, ref, watch } from 'vue';
import ScoreBoard from '@/components/ScoreBoard.vue'
import DiceTray from '@/components/DiceTray.vue'
import DiceSlot from '@/components/DiceSlot.vue';
import RerollBoard from '@/components/RerollBoard.vue';
import { onBeforeRouteLeave, useRouter } from 'vue-router';
import LiveBoard from '@/components/LiveBoard.vue';

const router = useRouter();
const diceCount = 5;
const bonusCutoff = 63;
const bonusPoint = 35;
const totalTurns = 12;
const remainTurns = ref(1);
const remainRerolls = ref(3);
const currentTurn = ref(0);
const ranking = ref([])
const entityList = ref([
    {
        name: "플레이어 1",
        top: Array.from({length: 6}, () => -1),
        bottom: Array.from({length: 6}, () => -1),
        total: 0
    },
    {
        name: "플레이어 2",
        top: Array.from({length: 6}, () => -1),
        bottom: Array.from({length: 6}, () => -1),
        total: 0
    }
]);
const diceList = ref({
    dice: [0, 0, 0, 0, 0],
    isFixed: [false, false, false, false, false],
});

function unLoadEvent(event){
    event.preventDefault();
    event.returnValue = '';
}

function sum(arr){
    let out = 0;
    arr.forEach(element => {
        if (element != -1){
            out += element;
        }
    });
    return out;
}

function getTotal(entity){
    let out = sum(entity.top) + sum(entity.bottom);
    if (sum(entity.top) >= bonusCutoff) 
        out += bonusPoint;
    return out;
}

function updateCurrentTurn(){
    currentTurn.value++;
    if (currentTurn.value % entityList.value.length == 0){
        currentTurn.value = 0;
        updateRemainTurns();
    }

}

function updateRemainTurns(){
    if (remainTurns.value < totalTurns) remainTurns.value++;
    else goResult();
}

function updateRanking(){
    if (entityList.value[0].total > entityList.value[1].total){
        ranking.value[0] = entityList.value[0].name;
        ranking.value[1] = entityList.value[1].name;
    }
    else {
        ranking.value[0] = entityList.value[1].name;
        ranking.value[1] = entityList.value[0].name;
    }
}

function updateScore(entityIdx, scoreIdx, score, isTop){
    if (isTop) entityList.value[entityIdx].top[scoreIdx] = score;
    else entityList.value[entityIdx].bottom[scoreIdx] = score;
    initDice();
    updateTotalScore();
    updateRanking();
    updateCurrentTurn();
}

function updateTotalScore(){
    entityList.value.forEach(entity => {
        entity.total = getTotal(entity);
    })
}

function updateRemainRerolls(){
    remainRerolls.value--;
}

// return: min ~ max 사이의 랜덤 정수값 1개
function rand(min, max){
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function rollDice(){
    for (let i = 0; i < diceCount; i++){
        if (!diceList.value.isFixed[i]){
            diceList.value.dice[i] = rand(1, 6);
        }
    }
    updateRemainRerolls();
}

function fixDice(index){
    console.log((index + 1) + " 번째 주사위 고정됨")
    diceList.value.isFixed[index] = true;
}

function releaseDice(index){
    console.log((index + 1) + " 번째 주사위 고정 해제됨")
    diceList.value.isFixed[index] = false;
}

function initDice(){
    for (let i = 0; i < 5; i++){
        diceList.value.dice[i] = 0;
        diceList.value.isFixed[i] = false;
    }
    remainRerolls.value = 3;
}

function goResult(){
    const mappedRanking = ranking.value.map(item => item);
    router.replace({
        name: 'Result',
        state: {
            ranking: mappedRanking,
        }
    });
}

onMounted(() => {
    window.addEventListener('beforeunload', unLoadEvent);
    updateRanking();
})

onBeforeUnmount(() => {
    window.removeEventListener('beforeunload', unLoadEvent);
}) 

onBeforeRouteLeave((to, from, next) => {
    if (to.name === "Result") next();
    else if (confirm("이 게임에서 나가시겠습니까? 진행 상황은 저장되지 않습니다.")){
        next();
    }
})

watch(entityList, () => {
    updateTotalScore();
})
</script>

<style scoped>
@import url("@/assets/css/singleplay.css");
</style>