import { createRouter, createWebHistory } from "vue-router";

import HomeView from '@/views/HomeView.vue'
import SingleplayView from '@/views/SingleplayView.vue'
import ScoreboardView from "@/views/ScoreboardView.vue";
import MultiplayView from "@/views/MultiplayView.vue";
import ResultView from "@/views/ResultView.vue";

const routes = [
    {
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/singleplay',
        name: 'Singleplay',
        component: SingleplayView
    },
    {
        path: '/multiplay',
        name: 'Multiplay',
        component: MultiplayView
    },
    {
        path: '/scoreboard',
        name: 'Scoreboard',
        component: ScoreboardView
    },
    {
        path: '/result',
        name: 'Result',
        component: ResultView,
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export {router}