<template>
    <div class="dicetray">
        <div class="dicetray-gray">
            <div class="dicetray-dark-1">
                <div class="dicetray-darkgray">
                    <div class="dicetray-dark-2">
                        <div class="dicetray-red">
                            <div v-if="props.remainRerolls != 3" class="dicetray-placeable">
                                <div v-for="(number, index) in props.diceList.dice" :key="index">
                                    <DiceObject v-if="!diceList.isFixed[index]" :diceNumber="number" class="dicetray-dice" 
                                    :style="diceState[index]" @click-dice="fixDice(index)"></DiceObject>
                                    <div v-else></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, defineProps, defineEmits, watch } from 'vue';
import DiceObject from './DiceObject.vue';

const props = defineProps({
    remainRerolls: {
        type: Number,
        default: 3
    },
    diceList: {
        dice: {
            type: Array,
            default (){
                return [0, 0, 0, 0, 0]
            }
        },
        isFixed: {
            type: Array,
            default (){
                return [false, false, false, false, false]
            }
        }
    }
})

const emits = defineEmits(['fixDice'])

const diceState = ref([
    {transform: 'rotate(0deg)', top: '0%', left: '0%'},
    {transform: 'rotate(0deg)', top: '0%', left: '0%'},
    {transform: 'rotate(0deg)', top: '0%', left: '0%'},
    {transform: 'rotate(0deg)', top: '0%', left: '0%'},
    {transform: 'rotate(0deg)', top: '0%', left: '0%'},
])

// return: min ~ max 사이의 랜덤 정수값 1개
function rand(min, max){
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function isOverlapped(ti, location1){
    const err = 25;
    let out = false;
    let location2 = [0, 0];
    console.log("");
    for (let i = 0; i < ti; i++){
        location2 = [parseInt(diceState.value[i].top.replace('%', '')), parseInt(diceState.value[i].left.replace('%', ''))]
        if ((Math.abs(location1[0] - location2[0]) <= err) && (Math.abs(location1[1] - location2[1]) <= err)){
            out = true;
            break;
        }
    }
    return out;
}

function randState(){
    let location = [0, 0];
    for (let i = 0; i < 5; i++){
        do {
            location = [rand(0, 100), rand(0, 100)];
        } while (isOverlapped(i, location))
        diceState.value[i].transform = 'rotate(' + rand(0, 360) + 'deg)';
        diceState.value[i].top = location[0] + '%';
        diceState.value[i].left = location[1] + '%';
    }
}

function fixDice(index){
    emits("fixDice", index);
}

watch(props.diceList.dice, () => {
    randState();
})

</script>

<style scoped>
@import url("@/assets/css/dicetray.css");
</style>