<template>
    <div class="diceslot">
        <div v-for="(number, index) in props.diceList.dice" :key="index" class="diceslot-entity">
            <DiceObject v-if="props.diceList.isFixed[index]" class="diceinfo" :diceNumber="number"
            @click-dice="releaseDice(index)"></DiceObject>
            <div v-else></div>
        </div>
    </div>
</template>

<script setup>
import { defineProps, defineEmits } from 'vue';
import DiceObject from './DiceObject.vue';

const props = defineProps({
    diceList: {
        dice: {
            type: Array,
            default (){
                return [0, 0, 0, 0, 0]
            }
        },
        isFixed: {
            type: Array,
            default (){
                return [false, false, false, false, false]
            }
        }
    }
})

const emits = defineEmits(['releaseDice'])

function releaseDice(index){
    emits('releaseDice', index)
}

</script>

<style scoped>
@import url("@/assets/css/diceslot.css");
</style>