<template>
  <div class="scoreboard">
    <div class="scoreboard-top">
      <div class="scoreboard-property">
        <ul class="tbl">
          <li class="scoreboard-turn">
            <span style="font-size: xx-large;">현재 턴수</span>
            <br>
            <span style="font-size: x-large;">{{ props.remainTurns }} / {{ props.totalTurns }}</span>
          </li>
          <li class="scoreboard-top-attr" v-for="(item, index) in header.top" :key="index">
            <span :tooltip="tooltipTexts.top[index]" flow="right">
              <img class="scoreboard-top-img" :src="require(`@/assets/images/dice_${index + 1}.png`)"/>
              {{ item }}
            </span>
          </li>
          <li class="scoreboard-subtotal">부분 합계</li>
          <li class="scoreboard-bonus">
            <span :tooltip="tooltipTexts.top[6]" flow="right">
              +{{ props.bonusPoint }}점 보너스
            </span>
          </li>
        </ul>
      </div>
      <div v-for="(entity, i) in props.entityList" :key="i" 
      :class="[currentTurn == i ? 'scoreboard-entity-current' : 'scoreboard-entity']">
        <ul class="tbl">
          <li class="scoreboard-header">{{ entity.name }}</li>
          <template v-for="(score, j) in entity.top" :key="j">
            <button class="scoreboard-top-attr-data" style="color: inherit;"
            v-if="currentTurn == i && score == -1" 
            @click="$emit('updateTopScore', i, j, tempScores.top[j], true)">{{ tempScores.top[j] }}</button>
            <button class="scoreboard-top-attr-data" style="color: black;" v-else-if="score != -1">{{ score }}</button>
            <button class="scoreboard-top-attr-data" v-else></button>
          </template>
          <li class="scoreboard-subtotal-data">{{ sum(entity.top) }} / {{ props.bonusCutoff }}</li>
          <li class="scoreboard-bonus-data" v-if="sum(entity.top) >= props.bonusCutoff">+ {{ props.bonusPoint }}</li>
          <li class="scoreboard-bonus-data" v-else></li>
        </ul>
      </div>
    </div>
    <div class="scoreboard-bonus-tip">
      <img class="scoreboard-top-img" style="margin-left: 10px; margin-right: 0px;" :src="require(`@/assets/images/dice_1.png`)"/>
      ~
      <img class="scoreboard-top-img" style="margin-right: 0px;" :src="require(`@/assets/images/dice_6.png`)"/>
      의 합이 {{ props.bonusCutoff }} 이상인 경우, 보너스 {{ props.bonusPoint }}점을 추가로 받습니다.
    </div>

    <div class="scoreboard-bottom">
      <div class="scoreboard-property">
        <ul class="tbl">
          <li class="scoreboard-bottom-attr" v-for="(name, i) in header.bottom" :key="i">
            <span :tooltip="tooltipTexts.bottom[i]" flow="right">
              <div v-if="bottomImageIdxs[i][0] != 0" class="scoreboard-bottom-img-group">
                <template v-for="(img_idx, j) in bottomImageIdxs[i]" :key="j">
                  <img class="scoreboard-bottom-img" :src="require(`@/assets/images/dice_${img_idx}.png`)"/>
                </template>
              </div>
              <div v-else></div>
              {{ name }}
            </span>
          </li>
          <li class="scoreboard-total">합계</li>
        </ul>
      </div>
      <div v-for="(entity, i) in props.entityList" :key="i" 
      :class="[currentTurn == i ? 'scoreboard-entity-current' : 'scoreboard-entity']">
        <ul class="tbl">
          <template v-for="(score, j) in entity.bottom" :key="j">
            <button class="scoreboard-bottom-attr-data" style="color: inherit;"
            v-if="currentTurn == i  && score == -1"
            @click="$emit('updateBottomScore', i, j, tempScores.bottom[j], false)">{{ tempScores.bottom[j] }}</button>
            <button class="scoreboard-bottom-attr-data" style="color: black;" v-else-if="score != -1">{{ score }}</button>
            <button class="scoreboard-bottom-attr-data" v-else></button>
          </template>
          <li class="scoreboard-total-data" >{{ entity.total }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, ref, watch } from 'vue';

const props = defineProps({
  currentTurn: {
    type: Number,
    default: -1
  },
  remainTurns: {
    type: Number,
    default: -1,
  },
  totalTurns: {
    type: Number,
    default: 12,
  },
  bonusCutoff: {
    type: Number,
    default: 63,
  },
  bonusPoint: {
    type: Number,
    default: 35,
  },
  diceList: {
    dice: {
      type: Array,
      default (){
          return [0, 0, 0, 0, 0]
      }
    },
    isFixed: {
      type: Array,
      default (){
        return [false, false, false, false, false]
      }
    }
  },
  entityList: {
    type: Array,
    default () {
      return []
    }
  }
})
const header = {
  top: ['에이스', '듀얼', '트리플', '쿼드', '펜타', '헥사'],
  bottom: ['초이스', '포커', '풀 하우스', '스몰 스트레이트', '라지 스트레이트', '요트'],
}
const tooltipTexts = {
  top: [
    "눈이 1인 주사위의 총합입니다.",
    "눈이 2인 주사위의 총합입니다.",
    "눈이 3인 주사위의 총합입니다.",
    "눈이 4인 주사위의 총합입니다.",
    "눈이 5인 주사위의 총합입니다.",
    "눈이 6인 주사위의 총합입니다.",
    "상단 항목의 점수 합계가 63점 이상일 때,\n35점을 추가로 얻습니다.",
  ],
  bottom: [
    "주사위 눈 5개의 총합입니다.",
    "동일한 주사위 눈이 4개 이상일 때,\n주사위 눈 5개의 총합입니다.",
    "주사위를 3개, 2개로 묶었을 때 각각의 묶음 안에서 주사위 눈이 서로 동일할 때,\n주사위 눈 5개의 총합입니다.",
    "이어지는 주사위 눈이 4개 이상일 때, 15점을 받습니다.",
    "이어지는 주사위 눈이 4개 이상일 때, 30점을 받습니다.",
    "모든 주사위 눈이 같으면 50점을 받습니다.",
  ]
}
const bottomImageIdxs =  [
  [0],
  [1, 1, 1, 1, 6],
  [1, 1, 6, 6, 6],
  [1, 2, 3, 4],
  [1, 2, 3, 4, 5],
  [6, 6, 6, 6, 6]
]
const tempScores = ref({
  top: Array.from({length: 6}, () => 0),
  bottom: Array.from({length: 6}, () => 0),
});

function sum(arr){
  let out = 0;
  arr.forEach(element => {
    if (element != -1){
      out += element;
    }
  });
  return out;
}

function getCounter(){
  let out = Array.from({length: 7}, () => 0);
  props.diceList.dice.forEach(d => {
    out[d]++;
  })
  return out;
}

function getChoice(){
  return sum(props.diceList.dice);
}

function getFoaK(counter){
  let out, one, four;
  four = one = 0;
  for (let i = 1; i <= 6; i++){
    if (counter[i] == 4) four = i;
    if (counter[i] == 1) one = i;
  }
  if (four != 0) out = 4 * four + one;
  else out = 0;
  return out;
}

function getFullHouse(counter){
  let out, two, three;
  two = three = 0;
  for (let i = 1; i <= 6; i++){
    if (counter[i] == 2) two = i;
    if (counter[i] == 3) three = i;
  }
  if (two != 0 && three != 0) out = 2 * two + 3 * three;
  else out = 0;
  return out;
}

function getSStraight(counter){
  let out, flag;
  for (let start = 1; start <= 3; start++){
    flag = true;
    for (let j = 0; j < 4; j++){
      if (counter[start + j] == 0){
        flag = false;
        break;
      }
    }
    if (flag) break;
  }
  if (flag) out = 15;
  else out = 0;
  return out;
}

function getLStraight(counter){
  let out, flag;
  for (let start = 1; start <= 2; start++){
    flag = true;
    for (let j = 0; j < 5; j++){
      if (counter[start + j] == 0){
        flag = false;
        break;
      }
    }
    if (flag) break;
  }
  if (flag) out = 30;
  else out = 0;
  return out;
}

function getYacht(counter){
  let out, flag;
  flag = false;
  for (let i = 1; i <= 6; i++){
    if (counter[i] == 5){
      flag = true;
      break;
    }
  }
  if (flag) out = 50;
  else out = 0;
  return out;
}

function getTempScores(){
  const counter = getCounter();
  for (let i = 1; i <= 6; i++){
    tempScores.value.top[i - 1] = i * counter[i];
  }
  tempScores.value.bottom[0] = getChoice();
  tempScores.value.bottom[1] = getFoaK(counter);
  tempScores.value.bottom[2] = getFullHouse(counter);
  tempScores.value.bottom[3] = getSStraight(counter);
  tempScores.value.bottom[4] = getLStraight(counter);
  tempScores.value.bottom[5] = getYacht(counter);
}

watch (props.diceList.dice, () => {
  getTempScores();
})

</script>

<style scoped>
@import url("@/assets/css/tooltip.css");
@import url("@/assets/css/scoreboard.css");
</style>